.clsResetPassPage {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 0.1em solid grey;
    max-width: 30%;
    min-height: 100%;
    margin-top: 10%;
    margin-left: 35%;
    border-radius: 0.75em;
    background-color: white;
}

.clsResetPassHeaderTitle {
    margin-left: 25%;
    margin-top: 10%;
    font-family: 'Roboto';
    font-weight: 700 !important;
}

.clsEmailInput {
    margin-left: 25%;
    width: 50%;
    padding: 0.75rem 0.75rem;
    margin-top: 5%;
    border: 0.1rem solid grey;
    border-radius: 0.25rem;
}

.clsResetPassBtn {
    width: 50%;
    margin-top: 5%;
    margin-left: 25%;
    margin-bottom: 5%;
    background-color: #0066ff !important;
    color: white !important;
}


.clsNeedAccount {
    display: flex;
    flex-direction: row;
    margin-top: 1%;
    margin-left: 43%;
}

.clsLoginResetBtn {
    margin-left: 45%;
    background-color: white;
    color: #0066ff;
    margin-bottom: 2%;
}

.clsSignUpColor {
    color: #0066ff;
}