.clsLoginPage {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 0.1em solid grey;
    max-width: 30%;
    min-height: 100%;
    margin-top: 15%;
    margin-left: 35%;
    border-radius: 0.75em;
    background-color: white;
}

.clsLoginHeaderTitle {
    margin-left: 40%;
    margin-top: 10%;
    font-weight: 700 !important;
}

.clsEmailInput,
.clsPassInput {
    margin-left: 25%;
    width: 50%;
    padding: 0.75rem 0.75rem;
    margin-top: 10%;
    border: 0.1rem solid grey;
    border-radius: 0.25rem;
}

.clsLoginBtn {
    width: 50%;
    margin-top: 10%;
    margin-left: 25%;
    margin-bottom: 10%;
    background-color: #0066ff !important;
    color: white !important;
}

.clsLoginNeedAccount {
    margin-top: 1%;
    margin-left: 45%;
}

.clsForgotPass {
    color: #0066ff;
    margin-left: 35%;
    margin-bottom: 5%;
}

.clsSignUpTxtColor {
    color: #0066ff;

}