.clsProfilePage {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 0.1em solid grey;
    max-width: 30%;
    min-height: 100%;
    margin-top: 20%;
    margin-left: 35%;
    border-radius: 0.75em;
    background-color: white;
}

.clsHeaderHeaderTitle {
    margin-left: 40%;
    margin-top: 3%;
    font-size: 30%;
    font-family: 'Roboto';
    font-weight: 700 !important;
}

.clsLoginBtn {
    width: 50%;
    margin-top: 4%;
    margin-left: 25%;
    margin-bottom: 3%;
    background-color: #0066ff !important;
    color: white !important;
}

.clsUserProfile {
    margin-top: 5%;
    margin-left: 3%;
}

.clsLogOutAccount {
    margin-top: 1%;
    margin-left: 49%;
    color: #279EFF;
    font-size: 100%;
}