/* Navbar.css */

body {
    margin: 0;
    font-family: 'PT Sans', sans-serif;
}

.navbar {
    background-color: #dadada;
    padding: 15px 0;
}

.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    color: black;
    font-size: 1.5rem;
    margin-left: 2%;
    cursor: pointer;
}

.menu {
    display: flex;
    cursor: pointer;
}

.menu a {
    text-decoration: none;
    color: black;
    margin-right: 2rem;
    font-size: 1rem;
}

.menu a:hover {
    text-decoration: none;
    color: white;
    margin-right: 2rem;
    font-size: 1rem;
}

.menu-icon {
    display: none;
}

.bar {
    width: 25px;
    height: 3px;
    background-color: white;
    margin: 6px 0;
    transition: 0.4s;
}

/* Media Query for Responsive Design */

